import React from "react";
const s = {
  navbar: {
    width: "100vw",
    height: 70,
    position: "fixed",
    left: 0,
    top: 0,
    backgroundColor: "#292929",
    opacity: 0.9
  },
  container: {
    position: "fixed",
    right: 10,
    top: 10,
    width: 50,
    height: 50,
    display: "flex",
    flexDirection: "column",
    backgroundColor: "#292929",
    borderRadius: 25,
    justifyContent: "center",
    alignItems: "center",
    boxShadow: "0px 0px 4px 0px gray",
    opacity: 0.9
  },
  line: {
    width: "50%",
    backgroundColor: "white",
    height: 3,
    margin: 2,
    transition: "all 0.5s"
  }
};
const Burger = props => {
  const { title } = props;
  const lines = props.isDrawerOPen
    ? [
        <div
          style={{
            ...s.line,
            transform: "rotate(45deg) translateY(3px) translateX(2px)"
          }}
        />,
        <div
          style={{
            ...s.line,
            transform: "rotate(-45deg) translateY(-3px) translateX(2px)"
          }}
        />,
        <div style={{ ...s.line, display: "none" }} />
      ]
    : [<div style={s.line} />, <div style={s.line} />, <div style={s.line} />];

  return (
    <div style={s.navbar}>
      <h3 style={{ padding: 5, color: "white" }}>{title}</h3>
      <div style={s.container} onClick={props.onClickMenuHandler}>
        {lines}
      </div>
    </div>
  );
};

export default Burger;



