import React from "react";
import Navigation from "../Navigation/Navigation";
import Burger from "../Burger/Burger";

const s = {
  drawer: {
    width: "100vw",
    height: "auto",
    position: "fixed",
    left: 0,
    backgroundColor: "#292929",
    transition: "0.5s top"
  }
};

const Drawer = props => {
  const { title, onClickLinkHanlder, onClickMenuHandler, isDrawerOpen } = props;
  if (isDrawerOpen) {
    s.drawer = { ...s.drawer, top: 0 };
  } else {
    s.drawer = { ...s.drawer, top: -250 };
  }
  return (
    <div style={s.drawer}>
      <Navigation
        title={title}
        onClickLinkHanlder={onClickLinkHanlder}
        flexDirection="row"
      />
      <Burger
        title={title}
        onClickMenuHandler={onClickMenuHandler}
        isDrawerOPen={isDrawerOpen}
      />
    </div>
  );
};

export default Drawer;



