import React from "react";
import skill from "./Content";

const style = {
  ul: {
    padding: "0",
    display: "flex",
    flexWrap: "wrap"
  }
};

const Skill = props => {
  const createListItem = array => {
    return array.map(item => (
      <li className="skill" key={item}>
        {item}
      </li>
    ));
  };

  const softSkill = createListItem(skill.softSkill);
  const techSkill = createListItem(skill.techSkill);

  return (
    <div className="main">
      <h4>Soft Skill</h4>
      <ul style={style.ul}>{softSkill}</ul>
      <h4>Technical Skill</h4>
      <ul style={style.ul}>{techSkill}</ul>
    </div>
  );
};

export default Skill;



