import React from "react";
import firebase from "../../../firebase";

let portfolioRef = null;
class PortfolioList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      portfolio: null
    };
  }

  componentDidMount() {
    this.getData();
  }

  componentWillUnmount() {
    this.setState({ portfolio: null });
  }

  getData() {
    portfolioRef = firebase.database().ref("portfolio");
    let { portfolio } = this.state;
    portfolioRef.on("value", data => {
      portfolio = data.val();
      this.setState({ portfolio });
    });
  }

  render() {
    let { portfolio } = this.state;
    if (!portfolio) return <i className="fa fa-refresh fa-spin" />;

    return (
      <div>
        <div className="record-container">
          {Object.keys(portfolio)
            .filter(key => portfolio[key].hidden !== 1)
            .reverse()
            .map((key, idx) => {
              return (
                <div className="record" key={idx}>
                  <span className="column pName">
                    <a href={portfolio[key].url} target="_blank">
                      {portfolio[key].name}
                    </a>
                  </span>
                  <span className="column pDesc">{`"${
                    portfolio[key].desc
                  }"`}</span>
                  <span className="column pTech">{`{ ${
                    portfolio[key].tech
                  } }`}</span>
                  <span className="column pGithub">
                    <a href={portfolio[key].github} target="_blank">
                      <i class="fa fa-github-alt" />
                    </a>
                  </span>
                </div>
              );
            })}
        </div>
      </div>
    );
  }
}

export default PortfolioList;



