import React from "react";
import PortfolioList from "./PortfolioList/PortfolioList";

class Portfolio extends React.Component {
  render() {
    const greet = this.props.children;
    const intro = " ";
    return (
      <div className="main">
        <h3>Portfolio</h3>
        <p>{greet}</p>
        <p>{intro}</p>
        <PortfolioList />
      </div>
    );
  }
}

export default Portfolio;



