import React from "react";
import about from "./Content";

const style = {
  p: {
    maxWidth: "500px",
    fontSize: "1em",
    textAlign: "justify"
  }
};

const About = () => {
  const content = about.descriptions.map(desc => {
    return <p style={style.p}>{desc}</p>;
  });

  return (
    <div className="main">
      <h3>About</h3>
      {content}
    </div>
  );
};

export default About;



