import React from "react";
import content from "./Content";
import { Link } from "react-router-dom";

const s = {
  h3: {
    color: "white",
    padding: 5
  },
  links: {
    display: "flex",
    justifyContent: "space-around",
    flexDirection: "column",
    listStyle: "none",
    padding: 5
  },
  link: {
    marginBottom: 20
  },
  social: {
    display: "flex",
    justifyContent: "space-around",
    listStyle: "none",
    padding: 5
  },
  hr: {
    border: "0",
    borderTop: "1px solid #363636"
  }
};

const Navigation = props => {
  const { onClickLinkHanlder, title } = props;
  const flexDirection = props.flexDirection || "column";

  const links = content.links.map(link => {
    return (
      <li style={s.link} key={link.href}>
        <Link to={link.href} onClick={onClickLinkHanlder}>
          {link.title}
        </Link>
      </li>
    );
  });

  const social = content.social.map(link => {
    return (
      <li key={link.href}>
        <a href={link.href} target="_blank">
          <i className={link.faClass} />
        </a>
      </li>
    );
  });

  return [
    <Link to="/">
      <h3 style={s.h3}>{title}</h3>
    </Link>,
    <ul style={{ ...s.links, flexDirection }}>{links}</ul>,
    <hr style={s.hr} />,
    <ul style={s.social}>{social}</ul>
  ];
};

export default Navigation;



